import React, { useState } from "react";
import axios from "axios";
import {
  SignalIcon,
  CheckCircleIcon,
  XCircleIcon,
  ArrowPathIcon,
} from "@heroicons/react/24/solid";

const TARGET_URL = "https://dorajhhub.github.io/Apk-download-and-news/";

const PingChecker = () => {
  const [latency, setLatency] = useState<number | null>(null);
  const [status, setStatus] = useState("ready");

  const checkServerStatus = async () => {
    setStatus("checking");
    setLatency(null);

    const startTime = Date.now();

    try {
      await axios.get(`${TARGET_URL}?_=${new Date().getTime()}`);

      const endTime = Date.now();
      const measuredLatency = endTime - startTime;

      setLatency(measuredLatency);
      setStatus("success");
    } catch (error) {
      setLatency(null);
      setStatus("error");
      console.error("Server check failed:", error);
    }
  };

  return (
    <div className="min-h-[70vh] flex items-center justify-center p-4">
      <div className="relative w-full max-w-2xl p-8 overflow-hidden text-center glass-card md:p-12 animate-fade-in">
        {/* Background Glow */}
        <div className="absolute top-0 w-64 h-64 -translate-x-1/2 rounded-full left-1/2 bg-indigo-500/20 blur-3xl -z-10 animate-pulse"></div>

        <div className="mb-10">
          <div className="inline-flex items-center justify-center w-20 h-20 mb-6 text-white shadow-lg rounded-2xl bg-gradient-to-br from-indigo-500 to-purple-600 animate-float">
            <SignalIcon className="w-10 h-10" />
          </div>
          <h2 className="mb-4 text-3xl font-extrabold text-gray-900 md:text-4xl dark:text-white">
              
          </h2>
          <p className="text-gray-600 dark:text-gray-300">
                   .
          </p>
        </div>

        {/* Status Display */}
        <div className="relative mb-10">
          <div
            className={`w-48 h-48 mx-auto rounded-full border-4 flex items-center justify-center transition-all duration-500 ${
              status === "checking"
                ? "border-indigo-400 border-t-transparent animate-spin"
                : status === "success"
                ? "border-green-500 shadow-[0_0_30px_rgba(34,197,94,0.3)]"
                : status === "error"
                ? "border-red-500 shadow-[0_0_30px_rgba(239,68,68,0.3)]"
                : "border-gray-200 dark:border-gray-700"
            }`}
          >
            <div
              className={`text-center transition-all duration-300 ${
                status === "checking" ? "animate-pulse" : ""
              }`}
            >
              {status === "ready" && (
                <span className="text-lg font-medium text-gray-400">Ready</span>
              )}
              {status === "checking" && (
                <span className="font-bold text-indigo-500">Checking...</span>
              )}
              {status === "success" && (
                <div className="animate-pop-in">
                  <div className="mb-1 text-4xl font-black text-gray-800 dark:text-white">
                    {latency}
                  </div>
                  <div className="text-sm font-bold tracking-wider text-green-500 uppercase">
                    ms
                  </div>
                </div>
              )}
              {status === "error" && (
                <div className="animate-shake">
                  <XCircleIcon className="w-12 h-12 mx-auto mb-1 text-red-500" />
                  <div className="text-sm font-bold text-red-500">Failed</div>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Action Button */}
        <button
          onClick={checkServerStatus}
          disabled={status === "checking"}
          className={`btn-premium w-full sm:w-auto min-w-[200px] flex items-center justify-center gap-2 mx-auto ${
            status === "checking" ? "opacity-70 cursor-not-allowed" : ""
          }`}
        >
          {status === "checking" ? (
            <>
              <ArrowPathIcon className="w-5 h-5 animate-spin" />
               ...
            </>
          ) : (
            <>
              <ArrowPathIcon className="w-5 h-5" />
               
            </>
          )}
        </button>

        {status === "success" && (
          <p className="mt-6 text-sm text-gray-500 dark:text-gray-400 animate-fade-in">
            <CheckCircleIcon className="inline w-4 h-4 mr-1 text-green-500" />
               .
          </p>
        )}
      </div>
    </div>
  );
};

export default PingChecker;
