import React from "react";
import { useNavigate } from "react-router-dom";
import { motion, useMotionValue, useMotionTemplate } from "framer-motion";
import { Sword, Map, Users, Sparkles } from "lucide-react";

const Rpg: React.FC = () => {
  const navigate = useNavigate();
  const isMobile = window.matchMedia("(max-width: 768px)").matches;

  const mouseX = useMotionValue(0);
  const mouseY = useMotionValue(0);

  function handleMouseMove(e: React.MouseEvent) {
    if (isMobile) return;
    mouseX.set(e.clientX);
    mouseY.set(e.clientY);
  }

  const dynamicBg = isMobile
    ? {
        background:
          "radial-gradient(600px at 50% 30%, rgba(236,72,153,0.15), transparent 70%)",
      }
    : {
        background: useMotionTemplate`
        radial-gradient(
          800px at ${mouseX}px ${mouseY}px,
          rgba(236,72,153,0.18),
          transparent 70%
        )
      `,
      };

  const background = useMotionTemplate`
    radial-gradient(
      800px at ${mouseX}px ${mouseY}px,
      rgba(236,72,153,0.18),
      transparent 70%
    )
  `;

  return (
    <div
      onMouseMove={handleMouseMove}
      className="relative min-h-screen overflow-hidden bg-[#0b0f19] text-white"
    >
      {/* Dynamic Background */}
      <motion.div
        className="absolute inset-0 pointer-events-none"
        style={{ background }}
      />

      {/* Back */}
      <button
        onClick={() => navigate(-1)}
        className="relative z-10 px-6 py-4 text-pink-400 hover:underline"
      >
         
      </button>

      {/* HERO */}
      <section className="relative z-10 pt-32 pb-40 text-center">
        <motion.h1
          initial={{ opacity: 0, y: 40 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          className="text-6xl font-black text-transparent md:text-7xl bg-gradient-to-r from-pink-400 to-purple-500 bg-clip-text"
        >
          RPG PROJECT
        </motion.h1>

        <div className="flex justify-center mt-6"></div>

        <motion.p
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.3 }}
          className="max-w-3xl mx-auto mt-10 text-xl text-slate-300"
        >
                  RPG
        </motion.p>
      </section>

      {/* FEATURES */}
      <section className="relative z-10 max-w-6xl px-6 py-24 mx-auto">
        <div className="grid gap-8 md:grid-cols-4">
          {[
            { icon: Sword, title: "", desc: "   " },
            { icon: Map, title: "", desc: "   " },
            { icon: Users, title: "", desc: " & " },
            { icon: Sparkles, title: "", desc: "  & " },
          ].map((f) => (
            <motion.div
              key={f.title}
              whileHover={{ y: -10 }}
              className="p-8 transition border rounded-2xl bg-white/5 border-white/10 hover:border-pink-400/40"
            >
              <f.icon className="w-10 h-10 text-pink-400" />
              <h3 className="mt-6 text-2xl font-bold">{f.title}</h3>
              <p className="mt-3 text-slate-300">{f.desc}</p>
            </motion.div>
          ))}
        </div>
      </section>

      {/* WORLD */}
      <section className="relative z-10 px-6 py-24 bg-gradient-to-b from-transparent to-black">
        <motion.div
          initial={{ opacity: 0, y: 60 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          className="max-w-6xl mx-auto"
        >
          <h2 className="mb-8 text-4xl font-extrabold"></h2>
          <p className="max-w-3xl text-lg text-slate-300">
              , ,     RPG.
          </p>

          <div className="grid gap-10 mt-12 md:grid-cols-2">
            <div className="h-64 rounded-2xl bg-gradient-to-br from-pink-500/40 to-purple-500/10" />
            <div className="h-64 rounded-2xl bg-gradient-to-br from-blue-500/40 to-cyan-500/10" />
          </div>
        </motion.div>
      </section>

      {/* CTA */}
      <section className="relative z-10 py-32 text-center bg-black">
        <h2 className="text-4xl font-extrabold">  </h2>
        <p className="mt-6 text-slate-400">  </p>
      </section>

      {/* ROADMAP */}
      <section className="relative z-10 px-6 py-24">
        <div className="max-w-5xl pl-10 mx-auto border-l border-white/20 space-y-14">
          {[["ALPHA(0.0.01)", "   "]].map(
            ([title, desc]) => (
              <motion.div
                key={title}
                initial={{ opacity: 0, x: -40 }}
                whileInView={{ opacity: 1, x: 0 }}
                viewport={{ once: true }}
                className="relative"
              >
                <div className="absolute -left-[52px] w-4 h-4 rounded-full bg-pink-500" />
                <h3 className="text-2xl font-bold">{title}</h3>
                <p className="mt-2 text-slate-300">{desc}</p>
              </motion.div>
            )
          )}
        </div>
      </section>
    </div>
  );
};

export default Rpg;
