import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  ArrowRightIcon,
  SparklesIcon,
  RocketLaunchIcon,
  ServerIcon,
} from "@heroicons/react/24/solid";

const Home = () => {
  interface NewsItem {
    id: number;
    title: string;
    content: string;
    date: string;
    game: string;
  }

  const [news, setNews] = useState<NewsItem[]>([]);

  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/news.json`, {
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        return response.json();
      })
      .then((data) => setNews(data.newsList))
      .catch((error) => console.error("Error fetching news:", error));
  }, []);

  const truncateContent = (content: string) => {
    if (content.length <= 100) return content;
    return content.substring(0, 100) + "...";
  };

  return (
    <div className="pb-20 space-y-20">
      {/* Hero Section */}
      <section className="relative min-h-[80vh] flex items-center justify-center overflow-hidden rounded-3xl mx-4 mt-4">
        {/* Background Elements */}
        <div className="absolute inset-0 bg-gradient-to-br from-indigo-500/10 via-purple-500/10 to-pink-500/10 dark:from-indigo-900/20 dark:via-purple-900/20 dark:to-pink-900/20 backdrop-blur-3xl"></div>
        <div className="absolute rounded-full top-1/4 left-1/4 w-96 h-96 bg-indigo-400/20 blur-3xl animate-float"></div>
        <div
          className="absolute rounded-full bottom-1/4 right-1/4 w-96 h-96 bg-purple-400/20 blur-3xl animate-float"
          style={{ animationDelay: "2s" }}
        ></div>

        <div className="relative z-10 max-w-4xl px-6 mx-auto text-center">
          <div className="inline-flex items-center gap-2 px-4 py-2 mb-8 border rounded-full bg-white/50 dark:bg-white/10 backdrop-blur-md border-white/20 animate-fade-in">
            <span className="relative flex w-3 h-3">
              <span className="absolute inline-flex w-full h-full bg-green-400 rounded-full opacity-75 animate-ping"></span>
              <span className="relative inline-flex w-3 h-3 bg-green-500 rounded-full"></span>
            </span>
            <span className="text-sm font-medium text-gray-600 dark:text-gray-300">
              Currently Developing
            </span>
          </div>

          <h1 className="mb-6 text-5xl font-extrabold tracking-tight md:text-7xl animate-fade-in">
            <span className="text-transparent bg-clip-text bg-gradient-to-r from-indigo-600 via-purple-600 to-pink-600 dark:from-indigo-400 dark:via-purple-400 dark:to-pink-400">
              Innovation
            </span>
            <br />
            <span className="text-gray-900 dark:text-white">
              meets Creativity
            </span>
          </h1>

          <p
            className="max-w-2xl mx-auto mb-10 text-xl leading-relaxed text-gray-600 md:text-2xl dark:text-gray-300 animate-fade-in"
            style={{ animationDelay: "0.2s" }}
          >
                  .
            <br className="hidden md:block" />
            jihucompany   .
          </p>

          <div
            className="flex flex-col items-center justify-center gap-4 sm:flex-row animate-fade-in"
            style={{ animationDelay: "0.4s" }}
          >
            <Link
              to="/nyangnyang"
              className="flex items-center justify-center w-full gap-2 btn-premium sm:w-auto group"
            >
              <span> </span>
              <ArrowRightIcon className="w-5 h-5 transition-transform group-hover:translate-x-1" />
            </Link>
            <Link
              to="/ping"
              className="w-full px-8 py-3 font-bold text-gray-700 transition-all border rounded-xl dark:text-white bg-white/50 dark:bg-white/10 backdrop-blur-md border-white/20 hover:bg-white/80 dark:hover:bg-white/20 sm:w-auto"
            >
                
            </Link>
          </div>
        </div>
      </section>

      {/* Latest Updates */}
      <section className="px-4 mx-auto max-w-7xl sm:px-6 lg:px-8">
        <div className="flex items-center justify-between mb-8">
          <h2 className="flex items-center gap-3 text-3xl font-bold text-gray-900 dark:text-white">
            <SparklesIcon className="w-8 h-8 text-yellow-500" />
            News
          </h2>
          <Link
            to="/news"
            className="font-medium text-indigo-600 dark:text-indigo-400 hover:underline"
          >
            View all news
          </Link>
        </div>

        <div className="grid grid-cols-1 gap-6 md:grid-cols-2 lg:grid-cols-3">
          {news.slice(0, 10).map((item, index) => (
            <Link
              to={`/news?id=${item.id}`}
              key={item.id}
              className="glass-card p-6 group cursor-pointer block hover:scale-[1.02] transition-transform duration-300"
            >
              <div className="bg-gradient-to-br from-white/100 to-white/20 dark:from-white/10 dark:to-white/35 group-hover:opacity-100">
                <div className="flex items-center justify-between mb-4">
                  <span className="px-3 py-1 text-xs font-medium text-indigo-800 bg-indigo-100 rounded-full dark:bg-indigo-900/30 dark:text-indigo-300">
                    {item.game === "nyangnyang"
                      ? ""
                      : item.game === "rpg"
                      ? "Rpg Adventure"
                      : ""}
                  </span>
                  <span className="text-sm text-gray-500 dark:text-gray-400">
                    {item.date}
                  </span>
                </div>
                <h3 className="mb-3 text-xl font-bold text-gray-900 transition-colors dark:text-white group-hover:text-indigo-600 dark:group-hover:text-indigo-400">
                  {item.title}
                </h3>
                <p className="text-sm text-black dark:text-gray-300 line-clamp-3">
                  {truncateContent(item.content)}
                </p>
              </div>
            </Link>
          ))}
        </div>
      </section>

      {/* Projects Bento Grid */}
      <section className="px-4 mx-auto max-w-7xl sm:px-6 lg:px-8">
        <div className="mb-12 text-center">
          <h2 className="section-title">Featured Projects</h2>
          <p className="section-subtitle">
                 .
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 auto-rows-[300px]">
          {/* NyangNyang Tycoon - Large Card */}
          <div className="relative overflow-hidden md:col-span-2 glass-card group">
            <div className="absolute inset-0 transition-opacity bg-gradient-to-br from-pink-500/20 to-purple-600/20 dark:from-pink-900/40 dark:to-purple-900/40 group-hover:opacity-100"></div>
            <div className="relative z-10 flex flex-col justify-between h-full p-8">
              <div>
                <div className="flex items-center justify-center mb-6 text-3xl shadow-lg w-14 h-14 rounded-2xl bg-white/90 dark:bg-white/10 backdrop-blur-xl">
                  
                </div>
                <h3 className="mb-2 text-3xl font-bold text-gray-900 dark:text-white">
                  
                </h3>
                <p className="max-w-md text-lg text-gray-600 dark:text-gray-300">
                       . 
                     !
                </p>
              </div>
              <div className="flex items-center justify-between">
                <div className="flex gap-2">
                  <span className="px-3 py-1 text-sm font-medium rounded-lg bg-white/50 dark:bg-black/30 backdrop-blur-md">
                    Simulation
                  </span>
                  <span className="px-3 py-1 text-sm font-medium rounded-lg bg-white/50 dark:bg-black/30 backdrop-blur-md">
                    Tycoon
                  </span>
                </div>
                <Link
                  to="/nyangnyang"
                  className="p-3 transition-colors bg-white rounded-full shadow-lg dark:bg-white/10 hover:bg-gray-100 dark:hover:bg-white/20"
                >
                  <ArrowRightIcon className="w-6 h-6 text-gray-900 dark:text-white" />
                </Link>
              </div>
            </div>
          </div>

          {/* BlastLoop - Tall Card */}
          <div className="relative overflow-hidden glass-card group">
            <div className="absolute inset-0 transition-opacity bg-gradient-to-br from-green-500/20 to-emerald-600/20 dark:from-green-900/40 dark:to-emerald-900/40 group-hover:opacity-100"></div>
            <div className="relative z-10 flex flex-col justify-between h-full p-8">
              <div>
                <div className="flex items-center justify-center mb-6 text-3xl shadow-lg w-14 h-14 rounded-2xl bg-white/90 dark:bg-white/10 backdrop-blur-xl"></div>
                <h3 className="mb-2 text-2xl font-bold text-gray-900 dark:text-white">
                  Rpg
                </h3>
                <p className="text-gray-600 dark:text-gray-300">
                   rpg 
                </p>
              </div>
              <Link
                to="/blastloop"
                className="w-full py-3 font-bold text-center transition-colors rounded-xl bg-white/50 dark:bg-white/10 hover:bg-white/80 dark:hover:bg-white/20 backdrop-blur-md"
              >
                Play Now
              </Link>
            </div>
          </div>

          {/* Coming Soon - Small Card */}
          <div className="relative flex flex-col items-center justify-center p-8 overflow-hidden text-center glass-card group">
            <div className="flex items-center justify-center w-16 h-16 mb-4 transition-transform bg-gray-100 rounded-full dark:bg-gray-800 group-hover:scale-110">
              <RocketLaunchIcon className="w-8 h-8 text-gray-400" />
            </div>
            <h3 className="mb-2 text-xl font-bold text-gray-900 dark:text-white">
              Coming Soon
            </h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">
                .
            </p>
          </div>

          {/* Stats - Wide Card */}
          <div className="flex items-center justify-around p-8 md:col-span-2 glass-card">
            <div className="text-center">
              <div className="mb-1 text-4xl font-extrabold text-indigo-600 dark:text-indigo-400">
                1+
              </div>
              <div className="text-sm text-gray-500 dark:text-gray-400">
                Released Games
              </div>
            </div>
            <div className="w-px h-12 bg-gray-200 dark:bg-gray-700"></div>
            <div className="text-center">
              <div className="mb-1 text-4xl font-extrabold text-purple-600 dark:text-purple-400">
                1+
              </div>
              <div className="text-sm text-gray-500 dark:text-gray-400">
                In Development
              </div>
            </div>
            <div className="w-px h-12 bg-gray-200 dark:bg-gray-700"></div>
            <div className="text-center">
              <div className="mb-1 text-4xl font-extrabold text-green-600 dark:text-green-400">
                24/7
              </div>
              <div className="text-sm text-gray-500 dark:text-gray-400">
                Server Uptime
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Home;
